* {
  box-sizing: border-box;
  font-size: 14px;
  font-family: "Myriad Pro", "OpenSans", Verdana, "PingFang SC", "Microsoft YaHei", sans-serif;
}

body {
  position: relative;
  margin: 0;
}

a {
  color: #009DE3;
  cursor: pointer;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none;
  }
}

.login-close {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 12px;
  height: 12px;
  cursor: pointer;
  background-image: url("../img/icon_close9x9_normal.png");
  background-repeat: no-repeat;
}

.login-content {
  width: 401px;
  height: 301px;
  border: solid 1px #009DE3;
}

.login-title {
  margin: 15px 0 0 0;
  font-size: 18px;
  color: #009DE3;
  text-align: center;
}

.default-login {
  width: 400px;
  height: 300px;
  padding: 20px 40px;
  position: relative;
  .user-info {
    display: block;
    width: 320px;
    height: 40px;
    margin: 14px 0;
    padding-left: 12px !important;
    background: #F4F4F4;
    border: 1px solid #EAEAEA;
    border-radius: 2px;
    font-size: 14px;
    color: #1A1A1A;
    &:focus {
      outline: none;
      background: #FFF !important;
      border: 1px solid #009DE3 !important;
      color: #1A1A1A !important;
    }
  }
  .password {
    @extend .user-info;
  }
  ul {
    margin-top: 14px;
    position: relative;
    li {
      display: inline-block;
      position: absolute;
      font-size: 12px;
      color: gray;
      list-style: none;
      &:first-child {
        left: 0;
      }
      &:nth-child(2) {
        right: 0;
      }
      a {
        font-size: 12px;
      }
    }
  }
}

.login-btn {
  width: 320px;
  height: 40px;
  color: white;
  border: 0;
  outline: none;
  background: #009DE3;
  border-radius: 2px;
  font-size: 14px;
  cursor: pointer;
  &:disabled {
    background: #7FCEF1;
    cursor: not-allowed;
  }
}

.qq-login {
  position: absolute;
  width: 400px;
}

.quick-login {
  margin: 15px 0 0 170px;
  font-size: 14px;
  color: #939392;
}

input.qq-login-btn {
  margin: -24px 0 0 234px;
  width: 130px;
  text-align: center;
  position: absolute;
  background: #009dd9;
  color: white;
  border: 0;
}

label.error-msg {
  display: none;
  width: 140px;
  height: 20px;
  font-size: 12px;
  color: #e34a4a;
}

.transition {
  position: absolute;
  display: none;
  top: 50%;
  left: 50%;
  width: 32px;
  height: 32px;
  margin: -16px 0 0 -16px;
  z-index: 99;
  background-image: url("../img/login_loading.gif");
}

.blur-loading {
  filter: blur(2px);
  -webkit-filter: blur(2px);
}
